<style lang="scss">
.container{
  table{ width: 100%;}
  td{ float:none;}
}

</style>
<template>
<div class="fr-w container">
  <h3 style="line-height:2em;margin-top:1em;">
  <el-button
      type="primary"
      @click.native="openFullScreen"
      v-loading.fullscreen.lock="fullscreenLoading">
      显示整页加载，3 秒后消失
    </el-button>
  </h3>

  <h3 style="line-height:2em;margin-top:1em;"> 非组件表单元素 </h3>

  <div class="bt-input"><input type="text" placeholder="input输入框"></div>
  <br>
  <div class="bt-input"><textarea  placeholder="textarea输入框"></textarea></div>

  <h3 style="line-height:2em;margin-top:1em;">静态表格，和 v-model 绑定 </h3>

  <table>
    <thead>
      <tr>
        <th>复选框</th>
        <th>下拉框</th>
        <th>复选框</th>
        <th>复选框</th>
      </tr>
    </thead>
    <tbody>
      <tr class="row">
        <td class="col-xl-3">
          <el-checkbox v-model="bcheck">选择框</el-checkbox>
        </td>
        <td class="col-xl-3">
          <el-select v-model="mselects" filterable clearable placeholder="请选择">
            <el-option v-for="item in options" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </td>
        <td class="col-xl-3">
          <el-checkbox v-model="bcheck">选择框</el-checkbox>
        </td>
        <td class="col-xl-3">
          <el-checkbox v-model="bcheck">选择框</el-checkbox>
        </td>
      </tr>
    </tbody>
  </table>
  <table>
      <tr>
        <td><el-input placeholder="请输入A" v-model="inputa"></el-input></td>
        <td><el-input placeholder="请输入B" v-model="inputb"></el-input></td>
        <td><address-box v-model="address" data-url="./static/data/address-data.json"></address-box></td>
      </tr>
    </table>

  <h3 style="line-height:2em;margin-top:1em;">Customer Table  v-for 列表中的表单不能使用 v-model 进行双向绑定，需要使用 vuex 进行数据管理</h3>

  <custom-table :data="tableData">
    <custom-row v-for="(item, index) in tableData" :index="index">
      <custom-column label="日期" prop="date" twidth="80">
        <el-input :value="item.date"/>
      </custom-column>
      <custom-column label="姓名" prop="name" twidth="80">
        <el-input :value="item.name"/>
      </custom-column>
      <custom-column label="选择" prop="choose" twidth="80">
        <el-select :value="item.choose" placeholder="请选择">
          <el-option v-for="opt in options" :label="opt.label" :value="opt.value"></el-option>
        </el-select>
      </custom-column>
      <custom-column label="勾选" prop="schecked" twidth="80">
        <el-checkbox :value="item.schecked" ></el-checkbox>
      </custom-column>
    </custom-row>
  </custom-table>

  <h3 style="line-height:2em;margin-top:1em;">EL-Table 表格组件</h3>

  <el-table :data="tableData" borderstyle="width: 100%" @selection-change="handleSelectionChange">
    <el-table-column type="selection" width="50" label="选择"></el-table-column>
    <el-table-column inline-template label="日期" width="120">
      <div>{{ row.date }}</div>
    </el-table-column>
    <el-table-column type="input" prop="name" label="姓名" width="120"></el-table-column>
    <el-table-column prop="address" label="地址" show-overflow-tooltip></el-table-column>
    <el-table-column type="select"  :option-list="options" prop="choose" label="下拉选择"></el-table-column>
    <el-table-column type="select"  :option-list="options" prop="choose" label="下拉选择"></el-table-column>
    <el-table-column type="checkbox" prop="schecked" label="可选"></el-table-column>
    <el-table-column type="checkbox" prop="schecked" label="可选"></el-table-column>
  </el-table>

  <router-view></router-view>
</div>
</template>
<script>
import ElCheckbox from 'components/checkbox';
import ElSelect from 'components/select';
import ElOption from 'components/option';
import ElInput from 'components/input';
import ElButton from 'components/button';
import ElTag from 'components/tag';
import ElTable from 'components/table';
import ElTableColumn from 'components/table-column';
import Tooltip from 'components/tooltip';
import CustomTable from 'components/custom-table'
import CustomRow from 'components/custom-row'
import CustomColumn from 'components/custom-column'
import AddressBox from 'components/address-box'

// 常量引入
const CONFIG = require('constant').CONFIG;

export default{
  name: 'home',
  components:{
   ElCheckbox, ElSelect, ElOption, ElInput, ElTable,
   ElTableColumn, ElButton, ElTag, Tooltip,
   CustomTable,CustomRow, CustomColumn, AddressBox
  },
  props: {

  },
  data () { return {
    load: true,
    bcheck: false,
    mselects: '选项1',
    options: [{
      value: '1',
      label: '黄金糕'
    }, {
      value: '2',
      label: '双皮奶'
    }, {
      value: '3',
      label: '蚵仔煎'
    }, {
      value: '4',
      label: '龙须面'
    }, {
      value: '5',
      label: '北京烤鸭'
    }],
    inputa: 'in A',
    inputb: 'in B',
    inputc: 'in C',
    address: '11-1101-110101',
    tableData: [{
      date: '2016-05-03',
      name: '王小虎',
      address: '上海市普陀区金沙江路 1518 弄',
      schecked: false,
      choose:'1'
    },{
      date: '2016-05-07',
      name: '王小虎',
      address: '上海市普陀区金沙江路 1518 弄',
      schecked: true,
      choose:'2'
    }],
    multipleSelection: [],
    fullscreenLoading: false
  }},
  methods:{
    handleClick() {
      console.log(1);
    },
    openFullScreen() {
      this.fullscreenLoading = true;
      setTimeout(() => {
        this.fullscreenLoading = false;
      }, 3000);
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    }
  }

}
</script>



