<style lang="scss">
@mixin displayAlign($align:left){
	display:block; 
	text-align:$align;
}
@mixin menuTap{
	cursor:pointer;
	width:100%;
	text-overflow: ellipsis;
}
%backgroundCenter{
	background-position: center center;
	background-repeat: no-repeat;
}
.main-nav{
	position: relative;
	height: 30px;
	width: 100%;
	background-color:#5d9cec;
	ul,li{
		padding:0; margin:0; list-style:none;
	}
	.main{ width: 90%; height: 30px; margin: 0 auto;}
	.menu-cell{display:inline-block; text-align:center;min-width:80px;}
	.menu-cell:hover{
		background-color:#5C676D;
	}
	.menu-cell:hover .subbox{ display: block; }
	.tap{
		position: relative;
		line-height: 30px;
		color: #fff;
		padding: 0 20px 0 10px;
		box-sizing: border-box;
		@include displayAlign(center);
		@include menuTap;
	}
	.tap:after{
		background-image: url(../../../assets/menu.png);
		@extend %backgroundCenter;
		content: '';
		position:absolute;
		top:0;
		right:0;
		width:20px;
		height:30px;
	}
	.menu-cell:hover .tap:after{
		background-image: url(../../../assets/menu_hover.png);
	}
	.menu-cell .tapnomore{padding:0 10px;}
	.main .menu-cell .tapnomore:after{ background-image: none;}

	.sub-tap{
		@include displayAlign(left);
		@include menuTap;
		font-size: 14px;
		line-height: 1.6em;
    border-bottom: 2px solid #5d9cec;
	}
	
	.ssub-tap{
		@include displayAlign(left);
		@include menuTap;
		display:block;
		margin: 5px 0;
	}
	.subbox{ 
		display: none;
		position:absolute; top:100%; left:0;
		z-index: 10;
		width: 100%;
		background-color:#f5f5f5;
	}
	.list-box{
		text-align: left;

	}
	.sub-cell{
		max-width: 14em;
		padding: 1em;
		display: inline-block;
		vertical-align: top;
	}
	.sub-tap:hover, .ssub-tap:hover{
		color:#1CCCFE;
	}

}
</style>
<template>
<nav id="main-navbody" class="main-nav">
<ul class="main container">
	<li class="menu-cell" @mouseover="menuAlign" v-for="om in cpMenusData">
		<span class="tap" @click="routeGo" :class="{tapnomore:!om.subMenu}" :url="om.url" v-text="om.label"></span>
		<div class="subbox">
			<ul class="list-box">
				<li class="sub-cell" v-for="sub in om.subMenu">
					<h3 v-if="sub.url" class="sub-tap" @click="routeGo" :url="sub.url" v-show="sub.label" v-text="sub.label"></h3>
					<h3 v-if="sub.route" class="sub-tap" @click="routeGo"  :route="sub.route" v-show="sub.label" v-text="sub.label"></h3>
					<span v-if="sub.url" class="ssub-tap" @click="routeGo" v-for="ssub in sub.subMenu" :url="ssub.url" v-text="ssub.label"></span>
					<span v-if="sub.route" class="ssub-tap" @click="routeGo" v-for="ssub in sub.subMenu" :route="ssub.route" v-text="ssub.label"></span>
				</li>
			</ul>
		</div>
	</li>
</ul>
</nav>
</template>
<script>
const MAIN_MENUS = require('constant').CONFIG.MAIN_MENUS;

export default{
  name: 'main-nav',
  props: {
    menusData:{
      type: Array,
			required:false,
      default: function(){
				return MAIN_MENUS;
			}
    }
  },
	computed:{
		// 过滤 subMenu
		cpMenusData () {
		  let data = this.menusData || MAIN_MENUS;
			for(let cell of data) {
				for(let sub of cell['subMenu']) {
					if(!sub['label'] || !(/[\W\w]+/i.test(sub['label']))){
						sub['label'] = false
					}
				}
				if(cell['subMenu'] && cell['subMenu'].length < 1){
					cell['subMenu'] = false
				}
			}
			return data;
		}
	},
  data () {
    return {}
  },
	methods:{
		// 菜单定位
		menuAlign (e) {
			var bodyEl = document.querySelector('body');
			var eOffsetLeft = e.currentTarget.offsetLeft;
			var listEl = e.currentTarget.querySelector('.list-box');
			
			if(bodyEl.clientWidth/2 > eOffsetLeft){
				listEl.style.marginLeft = eOffsetLeft + 'px';
			}else{
				listEl.style.marginRight = (bodyEl.clientWidth - eOffsetLeft - e.currentTarget.clientWidth) + 'px';
				listEl.style.textAlign = 'right';
			}
		},
		// 路由处理
		routeGo (e) {
			let _thisPath = /^\/$/.test(location.pathname) ? '/index.html' : '/'+location.pathname.match(/\w+\.html/i);
			let _Path = e.target.attributes['url'];
			let _route = e.target.attributes['route'];
			let _rPath =  _Path || _route;
			let _routePath = _route ? _route.value.match(/\w+\.html/i) : '/index.html';

			//判断是否是当前页面路由
			if(_route && _thisPath == _routePath) {
				if(!this.$router) throw "This main-nav component need vue-router plugin to ready!";
				this.$router.pusth(_route.value.replace(/\w+\.html/i, ''));
			}else if(_rPath){
				location.href = '//' + location.host 
					+ location.pathname.replace(/\w+\.html/i, '')
					+ _rPath.value.match(/\w+\.html/i) +'#'
					+ _rPath.value.replace(/\w+\.html/i,'');
			}
		}
	}
}
</script>


