<style>

</style>
<template>
  <div class="custom-table" @click="onclicked">
    <table class="custom-tbody" cellspacing="0" cellpadding="0" border="0">
      <tbody>
        <slot></slot>
      </tbody>
    </table>
  </div>
</template>

<script>
// 自定义 table 组件
import TableStore from './table-store';

let tableIdSeed = 1;

export default{
  name: 'custom-table',
  props: {
    data: {
      type: Array,
      default: function () {
        return [];
      }
    }
  },
  data(){
    const store = new TableStore(this);
    return {
      store
    }
  },
  components:{ },
  methods: {
    onclicked() {
      console.log('Table cStore This', this)
    }
  },
  created() {
    this.tableId = 'cus-table_' + tableIdSeed + '_';
  }
}
</script>



