<template>
<div class="address-box" v-clickoutside="handleClose">
  <div class="addrbox el-icon-caret-bottom">
    <input type="text" class="place-text" 
    v-model="addressLabel"
    :disabled="disabled"
    @focus="handleOpenBox"  
    :placeholder="placeholder" 
    readonly>
  </div>
  <transition name="md-fade-bottom">
  <div class="address-chooser" v-show="isShowAddrBox">
    <span class="close el-icon-close" @click="handleClose"></span>
    <ul class="address-tabs">
      <li :class="['tab', 'el-icon-arrow-down',{active:isActiveProvince}]" @click="tabProvince" v-text="provinceWord" v-show="isShowProvince"></li>
      <li :class="['tab', 'el-icon-arrow-down',{active:isActiveCity}]" @click="tabCity" v-text="cityWord" v-show="isShowCity"></li>
      <li :class="['tab', 'el-icon-arrow-down',{active:isActiveArea}]" @click="tabArea" v-text="areaWord" v-show="isShowArea"></li>
    </ul>
    <div class="address-contents">
      <ul class="address-province" v-show="isActiveProvince">
        <li v-for="item in provinceData" @click="clickProvince(item.c_area_code)" v-text="item.c_area_cname"></li>
      </ul>
      <ul class="address-city" v-show="isActiveCity">
        <li v-for="item in cityData" @click="clickCity(item.c_area_code)" v-text="item.c_area_cname"></li>
      </ul>
      <ul class="address-area" v-show="isActiveArea">
        <li v-for="item in areaData" @click="clickArea(item.c_area_code)" v-text="item.c_area_cname"></li>
      </ul>
    </div>
  </div>
  </transition>
</div>
</template>
<script>
// 地址选择组件
import { t } from 'src/locale';
import Clickoutside from 'src/utils/clickoutside';
const ADDRESS_DATA_URL = require('constant').CONFIG.ADDRESS_DATA_URL;

const getAddressData = function(_this) { debugger
  let _self =  _this ? _this : this;
  if(!_self.$data._data){
    $.getJSON(_self.dataUrl, function(data){
      _self.$data._data = data;
      _self.initAddress(_self.$data._data);
    })
  }else if(_self.data.length>0){
    _self.initAddress(_self.data);
  }
}

export default{
  name: 'address-box',
  directives: { Clickoutside },
  props:{
    dataUrl: {
      type: String,  //数据请求URL
      default:function(){
        return ADDRESS_DATA_URL;
      }
    },
    // label: String,    //数值对应的文字
    value: {},        //省市县数值
    disabled: Boolean, 
    data:{            //省市县全部数据
      type: Array,
      default: function(){
        return [];
      }
    },
    placeholder: {
      type: String,
      default() {
        return t('el.select.placeholder');
      }
    }
  },
  data(){
    return{
     _data: null,
     province: -1,
     city: -1,
     area: -1,
     provinceWord: '请选择',
     cityWord: '请选择',
     areaWord: '请选择',
     provinceData: null,
     cityData: null,
     areaData: null,
     addressLabel: '',
     addressValue: '',
     isShowProvince: true,
     isShowCity: false,
     isShowArea: false,
     isShowAddrBox: false,
     dataIsReady: false
  }},
  methods: {
    loadingData(){  // 如果未加载数据再次载入数据
      getAddressData.call(this);
    },
    initAddress(data, value){
      let addr = new String(this.value).split('-') ;
      this.dataIsReady = true;

      if(addr.length===3){
        let province = this.province = addr[0];
        let city = this.city = addr[1];
        let area = this.area = addr[2];

        if(data){
          this.provinceData = data;
          this.isShowProvince = true;
          for(let item of data) { // 省
            if(item.c_area_code == province){
              this.provinceWord = item.c_area_cname;
              //this.province = item.c_area_code;
              if(item.sub && item.sub.length > 0){

                this.cityData = item.sub;
                this.isShowCity = true;
                for(let subitem of item.sub){  // 市
                  if(subitem.c_area_code == city){
                    this.cityWord = subitem.c_area_cname;
                   // this.city = subitem.c_area_code;
                    if(subitem.sub && subitem.sub.length > 0){

                      this.areaData = subitem.sub;
                      this.isShowArea = true;
                      for(let ssub of subitem.sub){  // 区
                         if(ssub.c_area_code == area){
                            this.areaWord = ssub.c_area_cname;
                            //this.area = subitem.c_area_code;
                            this.addressLabel = this.provinceWord + '-' +this.cityWord + '-' + this.areaWord;
                            break;
                         }
                       }
                    }
                    break;
                  }
                }
              }
             break;
            }
          }
        }
      }

    },
    handleOpenBox(e){
      this.isShowAddrBox = true;
    },
    handleClose(e){
      this.isShowAddrBox = false;
    },
    tabProvince(){
      this.isShowArea = false;
      this.isShowCity = false;
      this.city = -1;
      this.area = -1;
      this.areaWord= this.cityWord= this.provinceWord= "请选择";
    },
    tabCity(){
      this.isShowArea = false;
      this.isShowCity = true;
      this.area = -1;
      this.areaWord= this.cityWord="请选择";
    },
    tabArea(){
      this.areaWord="请选择"
    },
    clickProvince(code){
      let data = this.provinceData;
      if(data){
        for(let item of data) {
          if(item.c_area_code == code){
            this.provinceWord = item.c_area_cname;
            this.province = code;
            if(item.sub && item.sub.length > 0){
              this.cityData = item.sub;
              this.isShowCity = true;
            }else{
              this.cityData = null;
            }
            break;
          }
        }
      }
    },
    clickCity(code){
      let data = this.cityData;
      if(data){
        for(let item of data) {
          if(item.c_area_code == code){
            this.cityWord = item.c_area_cname;
            this.city = code;
            if(item.sub && item.sub.length > 0){
              this.areaData = item.sub;
              this.isShowArea = true;
            }else{
              this.areaData = null;
            }
            break;
          }
        }
      }
    },
    clickArea(code){
      let data = this.areaData;
      if(data){
        for(let item of data) {
          if(item.c_area_code == code){
            this.areaWord = item.c_area_cname;
            this.area = code;
            this.isShowAddrBox = false;

            this.addressValue = this.province + '-' +this.city + '-' + this.area;
            this.addressLabel = this.provinceWord + '-' +this.cityWord + '-' + this.areaWord;
            break;
          }
        }
      }
    }
  },
  watch:{
    addressValue(val) {
      this.$emit('input', val);
      this.$emit('change', val);
    }
  },
  computed: {
     isActiveProvince(){
       return this.isShowCity ? !this.isShowCity : this.isShowProvince;
     },
     isActiveCity(){
       return this.isShowArea ? !this.isShowArea : this.isShowCity;
     },
     isActiveArea(){
       return this.isShowArea;
     }
  },
  mounted() {
    getAddressData.call(this);
  }
}
</script>



