<style lang="scss"> </style>
<template>
<header class="main-header container">
  <div class="fr-w header-box">
    <div class="header-left">
      <img :src="logolink">
    </div>
    <div class="header-right">
      <span>欢迎您： 总公司</span>
      <span class="nor-btn" v-text="nowuser">匿名</span>
      <span>现在是：</span>
      <span v-text="nowdate"> 0000-000-000</span>
      <span class="nor-btn">修改密码</span>
      <span class="nor-btn">退出</span>   
    </div>
  </div>
</header>
</template>
<script>
export default{
  name: 'main-header',
  props: {
    nowuser: String,
    nowdate: String
  },
  data () {
    return {
      logolink: 'static/img/logo.png'
    }
  }
}
</script>


