<template>
<td class="cus-column"
 @input="updateColumnValue" @change="updateColumnValue"><slot></slot></td>
</template>
<script>
// 自定义 table column 组件

export default{
  name: 'custom-column',
  props:{
    label: String,
    prop: String,
    twidth: [String, Number]
  },
  data(){
    return{
     value: {}
    }
  },
  methods: {
    //更新单元格的值
    updateColumnValue(val) {
      console.log('TD已经改变:', val)
    }
  },
  computed: {
    owner() {
      let parent = this.$parent;
      while (parent && !parent.tableId) {
        parent = parent.$parent;
      }
      return parent;
    }
  },
  mounted() {

  }
}
</script>



