import './index.scss';
import Vue from 'vue';
import VueRouter from 'vue-router';
import Vuex from 'vuex';
import { MainHeader, MainNav, Loading }   from 'components';
import { Welcome, Home, Tables} from './parts';

//使用指令
Vue.use(Loading.directive);

/********  数据管理  ********/
Vue.use(Vuex);
const newstore = new Vuex.Store({
  state:{
    routeTo: '/'
  },
  getters: {
    cptRoute (state) {
      return state.routeTo;
    }
  },
  mutations:{
    chgRoute(state, playload){
       state.routeTo = playload.routeTo;
    }
  }
})

/********  路由管理  ********/
Vue.use(VueRouter);
const newrouter = new VueRouter({
  routes: [
    {
      path: '/',
      component: Welcome
    },
    {
      path: '/home',
      component:Home,
      children:[{
        path: 'tables',
        component: Tables
      }]
    }
  ]
});

/********  主页面逻辑  ********/
var vum = new Vue({
  el: '#main-box',
  store:newstore,
  router:newrouter,
  components: {
    MainHeader, MainNav, Welcome,Home
  },
  data: {
    check: true,
    fullscreenLoading: false
  },
  methods: {

  },
  watch: {

  }
});
console.log('PageVue Object：', vum)



